import axios from 'axios';
import {
    createStore
} from 'vuex';

// Create a new store instance.
const store = createStore({
    state() {
        return {
            name: 'kim',
            age: 20,
            likes: 10,
            clickLike: false,
            more: {},
        }
    },
    mutations: {
        이름변경(state) {
            state.name = 'park'
        },
        나이증가(state, payload) {
            state.age += payload;
        },
        좋아요증가(state) {
            if (state.clickLike == false) {
                state.likes++;
                state.clickLike = true;
            } else {
                state.likes--;
                state.clickLike = false;
            }
        },
        setMore(state, data){
            state.more = data
        },
    },actions:{
        getData(context){
            axios.get("https://codingapple1.github.io/vue/more0.json").then((a)=>{
                console.log(a.data)
                context.commit('setMore',a.data)
            })
        }
    },

})

export default store;