<template>
    <div>

        <div  v-if="step == 0">
            <Post :data="data[i]" v-for="(a,i) in data" :key="i" />
        </div>

        <!-- 필터선택페이지 -->
        <div  v-if="step == 1">
            <div :class="선택한필터" class="upload-image" :style="`background:url('${uplode}')no-repeat center center`"></div>
            <div class="filters" >
                <FilterBox :uplode="uplode" :fillters="a" v-for="(a,i) in fillters" :key="i">
                    <template v-slot:a>{{a}}</template>
                    <!-- <template v-slot:default="msg"><span>{{msg.msg}}</span></template> -->
                </FilterBox>
            </div>
        </div>

        <!-- 글작성페이지 -->
        <div  v-if="step == 2">
            <div  :class="선택한필터" class="upload-image" v-if="step == 2" :style="`background:url('${uplode}')no-repeat center center`"></div>
            <div class="write">
                <input type="text" @input="$emit('name',$event.target.value)">이름
                <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
            </div>
        </div>


    </div>
</template>

<script>
    import Post from "./VuestagramPost.vue";
    import FilterBox from "./FilterBox.vue";
    import fillters from "../assets/fillter";


    export default {
        name: 'VuestagramContainer',
        data(){
            return{
                fillters:fillters,
            }   
        },
        components: {
            Post,
            FilterBox,
        },
        props: {
            data: Array,
            step:Number,
            uplode:String,
            선택한필터:String,
        },
        
    }
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;

}
</style>