<template>

  <div class="header">
    <ul class="header-button-left">
      <li @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step != 2" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <p>{{name}} {{작명}}</p>

  <h4>안녕 {{$store.state.name}} 나이: {{$store.state.age}} </h4>
  <button @click="$store.commit('이름변경')">버튼</button>
  <button @click="$store.commit('나이증가',100)">나이증가버튼</button>


  <p>{{$store.state.more}}</p>
  <button @click="$store.dispatch('getData')">더보기</button>


  <Container @write="작성한글 = $event" @name="이름 = $event" :선택한필터="선택한필터" :data="data" :step="step" :uplode="uplode" />

  <button @click="more" v-if="show == true">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>



</template>

<script>
  import Container from "./components/VuestagramContainer";
  import data from "./assets/data.js";
  import axios from 'axios';
import { mapState } from 'vuex';

  export default {
    name: 'App',
    data() {
      return {
        step: 0,
        data: data,
        point: 0,
        show: true,
        uplode: "",
        선택한필터 : "",


      }
    },
    mounted(){
      this.emitter.on('ClickFillter',(data)=>{
        // console.log(data);
        this.선택한필터 = data;
      });
    },
    components: {
      Container,
    },
    computed:{
      name(){
        return this.$store.state.name
      },
      ...mapState(['name','age','likes']),
      ...mapState({작명:'name'})
    }
    ,
    methods: {
      more() {
        axios.get("https://codingapple1.github.io/vue/more" + this.point + ".json")
          .then((req) => {
            this.data.push(req.data);
            this.point++;
          })
          .catch((err) => {
            if (err) {
              this.show = false
            }
          })
      },
      upload(e) {
        let 파일 = e.target.files;
        let url = URL.createObjectURL(파일[0]);
        // console.log(url);
        this.uplode = url;
        this.step = 1;
      },
      publish() {
        var 내개시물 = {
          name: this.이름,
          userImage: "https://placeimg.com/200/200/people",
          postImage: this.uplode,
          likes: 20,
          date: "Apr 20",
          liked: false,
          content: this.작성한글,
          filter: this.선택한필터,
        };
        this.data.unshift(내개시물);
        this.step = 0;

      }
    },

  }
</script>

