<template>
    <div @click="fire" class="filter-item" :class="fillters" :style="`background:url(${uplode});background-size: cover;`">
        <slot name="a"></slot>
        <!-- <slot :msg="msg"></slot> -->
    </div>
</template>

<script>

    export default {
        name : "FilterBox",
        data(){
            return{
                msg:'hello',
            }
        },
        props:{
            uplode:String,
            fillters:String,
        },
        methods:{
            fire(){
                this.emitter.emit('ClickFillter',this.fillters);
            }
        },

    }
</script>

<style>
    .filter-item {
        width: 100px;
        height: 100px;
        margin: 10px 10px 10px auto;
        padding: 8px;
        display: inline-block;
        color: white;
        background-size: cover;
        background-position: center;
    }
</style>